.newsletter {
  background-color: #1e96ba;
}
.newsletter__left {
  width: 60rem;
}
.newsletter__right {
  border-bottom: 2px solid #2c2c2c;
}
.newsletter-input {
  padding: 1.5rem 0;
  border: none;
  display: inline !important;
  width: 90%;
  background-color: transparent;
}
.newsletter-input:focus {
  outline: none;
}
.newsletter-input::placeholder {
  color: #2c2c2c;
  opacity: 1;
  font-size: 1.6rem;
  font-weight: 600;
}